import React from 'react';
import Header from '../layouts/Header';
import Footer from '../layouts/Footer';
import { Host } from '../../helpers/comman_helper';
import { Link } from 'react-router-dom';
export default function PageNotFound() {
    return (
        <>
            <Header />
            <div class="container py-5 mb-lg-3">
                <div class="row justify-content-center pt-lg-4 text-center">
                    <div class="col-lg-5 col-md-7 col-sm-9">
                        <img class="d-block mx-auto mb-5" src={Host + "/uploads/images/404.png"} width="340" alt="404 Error" />
                        <h1 class="h3">404 error</h1>
                        <h3 class="h5 fw-normal mb-4">We can't seem to find the page you are looking for.</h3>
                        <p class="fs-md mb-4">
                            <u>Here are some helpful links instead:</u>
                        </p>
                    </div>
                </div>
                <div class="row justify-content-center">
                    <div class="col-xl-8 col-lg-10">
                        <div class="row">
                            <div class="col-sm-4 mb-3">
                                <Link class="card h-100 border-0 shadow-sm" to={'/'}>
                                    <div class="card-body">
                                        <div class="d-flex align-items-center"><i class="ci-home text-primary h4 mb-0"></i>
                                            <div class="ps-3">
                                                <h5 class="fs-sm mb-0">Homepage</h5>
                                                <span class="text-muted fs-ms">Return to homepage</span>
                                            </div>
                                        </div>
                                    </div>
                                </Link>
                            </div>
                            <div class="col-sm-4 mb-3">
                                <Link class="card h-100 border-0 shadow-sm" to={'/category'}>
                                    <div class="card-body">
                                        <div class="d-flex align-items-center">
                                            <i class="ci-search text-success h4 mb-0"></i>
                                            <div class="ps-3">
                                                <h5 class="fs-sm mb-0">By Category</h5>
                                                <span class="text-muted fs-ms">get news by category</span>
                                            </div>
                                        </div>
                                    </div>
                                </Link>
                            </div>
                            <div class="col-sm-4 mb-3">
                                <Link class="card h-100 border-0 shadow-sm" to={'/trending'}>
                                    <div class="card-body">
                                        <div class="d-flex align-items-center"><i class="ci-help text-info h4 mb-0"></i>
                                            <div class="ps-3">
                                                <h5 class="fs-sm mb-0">Help &amp; Support</h5>
                                                <span class="text-muted fs-ms">Visit our help center</span>
                                            </div>
                                        </div>
                                    </div>
                                </Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <Footer />
        </>
    )
}
